/** @property $errors */

/** @property entity */

/** @method update */

/** @method store */
export default {
  name: "UsersEdit",
  beforeCreate: function beforeCreate() {
    this.$bind.mapGetters('Auth', {
      userId: 'entity.id'
    });
    this.$bind.mapGetters('UserResource', {
      entity: 'entity'
    });
    this.$bind.mapActions('UserResource', {
      isLoading: 'isLoading',
      update: 'update',
      store: 'store'
    });
    this.$bind.mapState('UserResource', {
      $request: '$request',
      $errors: '$errors',
      roles: 'roles'
    });
  },
  created: function created() {
    this.$errors.clear();
  },
  beforeDestroy: function beforeDestroy() {
    this.$errors.clear();
  }
};